<template>
  <div class="patients">
    <mynav></mynav>
    <h2 class="title"><p><router-link to="/home" style="margin-right: 14px;"><i class="iconfont icon-left"></i></router-link>{{ projectInfo.u_projectName }}</p></h2>
    <h5 class="describe"><p></p></h5>
    <div class="flex-row">
      <div class="content">
        
        <patient-base class="create" :projectid="projectid"></patient-base>

        <div class="show-area">
          <div class="tools">
            <span></span>
            <m-button type="blue" :class="{disabled: disabled}" @click="deletePatients"></m-button>
          </div>
          <div class="show-container">
              <div class="recordSheet fontBold">
                  <div class="flex-row">
                    <input type="checkbox" id="allpatient"/>
                    <label for="allpatient" @click="allChecked"></label>
                  </div>
                  <div class="patient-name"></div>
                  <div class="patient-sex"></div>
                  <div class="patient-birthday"></div>
                  <div class="join-data"></div>
                  <div class="create-date"></div>
              </div>
          </div>
          <div class="show-container"  v-for="(item, index) in patients" :key="item.u_MDID">
              <div class="recordSheet">
                  <div class="flex-row">
                    <input type="checkbox" :id="item.u_MDID" :value="item.u_MDID" v-model="checkList" />
                    <label :for="item.u_MDID"></label>
                  </div>
                  <div class="patient-name"><router-link :to="'/project/'+ projectid + '/patient/'+ item.u_MDID" v-text="item.u_patientname"></router-link></div>
                  <div class="patient-sex" v-text="item.u_gender == '' ? '' : item.u_gender == '1' ? '' : ''"></div>
                  <div class="patient-birthday" v-text="item.u_birthday"></div>
                  <div class="join-data" v-text="item.u_jointime"></div>
                  <div class="create-date" v-if="item.u_updatetime !== ''">{{ item.u_updatetime | formatDate }}</div>
                  <div class="create-date" v-if="item.u_updatetime === ''">{{ item.u_createtime | formatDate }}</div>
              </div> 
          </div>
        </div>

        <div style="padding: 10px 0;text-align: center;"><pagination :total="patient_total" :current.sync="pagenum" :pagesize="30" @change="changsite"></pagination></div>
      </div>
    </div>
  </div>
</template>

<script>
  import API from '../api.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'
  import PatientBase from '../components/PatientBase'
  import Pagination from '../lib/page.vue'

  export default {
    name: 'Patients',
    components: {
      Mynav,
      MButton,
      Pagination,
      PatientBase
    },
    data () {
      return {
        username: window.sessionStorage.getItem('username'),
        projectid: '',
        projectInfo: {},
        pagenum: 1,
        patient_total: null,
        patients: [],
        disabled: true,
        checkList: []
      }
    },
    created () {
      this.projectid = this.$route.params.projectid
      this.pagenum = parseInt(this.$route.params.pagenum)
      this.getProjectInfo()
      this.getProjectPatients()
    },
    watch: {
      // 
      '$route': 'getProjectPatients',
      //  checcheckList 
      checkList (val, oldVal) {
        val.length > 0 ? this.disabled = false : this.disabled = true
      }
    },
    methods: {
      changsite () {
        let pagenum = arguments[0]
        this.pagenum = pagenum
        this.$router.push({name: 'PatientList', params: {pagenum: pagenum}})
      },
      getProjectInfo () {
        API.GetProjectInfo({projectId: this.projectid, userId: this.$root.userid}).then(response => {
          this.projectInfo = response[0]
        }).catch(err => {
          console.log(err)
        })
      },
      getProjectPatients () {
        this.patients = []
        API.GetProjectPatients({projectid: this.projectid, pagenum: this.pagenum}).then(response => {
          this.patient_total = Number(response.allnum)
          response.patientList.map(n => this.patients.push(n))
        }).catch(err => {
          console.log(err)
        })
      },
      /*  */
      allChecked () {
        if (this.checkList.length !== this.patients.length) {
          this.checkList = []
          for (let p in this.patients) {
            this.checkList.push(this.patients[p].u_MDID)
          }
        } else {
          this.checkList = []
        }
      },
      /*  */
      deletePatients () {
        if (this.disabled === true) { return }
        let vm = this
        this.confirm({
          title: '',
          message: '',
          onConfirm () {
            API.DeletePatients({userid: vm.$root.userid, mdid: vm.checkList.join(',')}).then(response => {
              vm.getProjectPatients()
            }).catch(err => {
              console.log(err)
            })
          }
        })
      }
    }
  }
</script>

<style scoped>
  .title {
    background: #468df1;
    height: 48px;
    color: #fff;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 48px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.23);
  }

  .title>p {
    width: 650px;
    margin: 0 auto;
  }

  .title i {
    color: #fff;
  }

  .describe {
    height: 50px;
    line-height: 50px;
    margin-bottom: 30px;
    color: #777;
    font-size: 16px;
    font-weight: 600;
    /*font-family: "";*/
  }

  .describe p {
    width: 620px;
    margin: 0 auto;
    padding-left: 30px;
  }

  .content {
    width: 650px;
    margin: 0 auto;
  }

  /*style*/
  .show-area{
    background: #fff;
    padding: 0 25px;
  }

  /* tools */
  .tools{
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
    border-bottom: 1px solid #eee;
  }

  .tools .button.disabled{
    opacity: .4;
    cursor: default;
  }
  /* patient */
  .fontBold{
    font-weight: 700;
  }
  .recordSheet.fontBold:hover{
    background: #f4f6f8
  }
  .show-container{
    border-bottom: 1px #eee solid;
    width: 100%;
    line-height: 2;
  }
  .show-container:frist-child:hover{
    background: #f4f6f8;
  }
  .recordSheet:hover{
    background: #ccc;
  }
  .recordSheet{
    display: table;
    text-align: center;
    padding: .5em 0;
  }
  
  .patient-name{
    display: table-cell;
    width: 8em;
  }
  .patient-name a{
    color: #3bb5ed;
    text-decoration: underline;
  }
  .patient-sex{
    display: table-cell;
    width: 4em;
  }
  .patient-birthday{
    display: table-cell;
    width: 8em;
  }
  .join-data{
    display: table-cell;
    width: 10em;
  }
  .create-date{
    display: table-cell;
    width: 12em;
  }
  .creater{
    display: table-cell;
    width: 8em;
  }

</style>


