<template>
  <div id="create-new">
    <mynav></mynav>
    <h2 class="title"><router-link :to="'/project/' + projectid + '/patients/1'" style="margin-right: 15px;"><i class="iconfont icon-left"></i></router-link></h2>
    
    <!---->
    <div class="fixed-right" :style="{left: content_width + 'px'}">
      <div class="temp-menu">
        <div v-for="t in templates" :class="{active: t.u_templateid === templateid}" @click="changeTemplate(t.u_templateid)">
          <p>{{ t.u_templatename }}</p>
          <span :class="{active: t.u_templateid === templateid}"></span>
        </div>
      </div>
      <router-link :to="'/project/' + projectid + '/patients/1'" class="back-btn btn"></router-link>
    </div>

    <!---->
    <div class="content" id="records">
      <transition name="fade">
        <component class="input-area" v-bind:is="mycomponent" :mdid="mdid" :templateid="templateid" :isnew="true" @putsever="setRecords(arguments[0])"></component>
      </transition>
      <transition name="fade">
        <record-bar v-if="addState" :imgshow="imgState" :remarkshow="remarkState" @putsever="setRemarks(arguments[0])"></record-bar>
      </transition>
      <div class="bar flex-row" v-if="addState">
        <div class="flex-row">
          <i class="iconfont icon-beizhu add-remark" @click="remarkState = true"></i>
          <i class="iconfont icon-tupian add-remark" @click="imgState = true"></i>
        </div>
        <m-button type="blue" :loading="addBtnState" @click="putData"></m-button>
      </div>

      <div class="cards flex-col">
        <div class="card base-info">
          <p> <span>{{ patientBase.u_patientname }}</span></p>
          <p> <span>{{ patientBase.u_gender==='1' ? '' : '' }}</span></p>
          <p> <span>{{ patientBase.u_birthday }}</span></p>
          <p> <span>{{ patientBase.u_jointime }}</span></p>
          <p> <span>{{ patientBase.u_createtime | formatDate }}</span></p>
        </div>
        <transition-group name="list">
          <div class="card" v-for="r in records" v-bind:key="r">
            <div class="card-header flex-row" style="justify-content: space-between; align-items: center;">
              <span>{{ patientBase.u_patientname + ' > ' + r.type }}</span>
              <span style="font-size: 12px; color: #777;">{{ r.update_time | formatDate }}</span>
            </div>
            <div class="card-main" @click="changeRecord(r.recordid, r.templateid)">
              <p v-if="val != ''" v-for="(val, index) in r.data">{{ index + ' ' + val }}</p>
            </div>
            <div class="card-remark text flex-row" v-if="r.remark != ''" >
              <p>{{ r.remark }}</p>
            </div>
            <div class="card-remark imgs flex-row">
              <img :src="'http://' + url" class="image-btn"  v-for="url in r.images" />
            </div>
            <div class="card-footer">
              
            </div>
          </div>
        </transition-group>
      </div>
    </div>

  </div>
</template>

<script>
  import Vue from 'vue'
  import API from '../api.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'
  import Loading from '../lib/Loading'
  import Temp from '../template/index'
  import PatientBase from '../components/PatientBase'
  import RecordBar from '../components/RecordBar.vue'

  export default {
    name: 'Create',
    components: {
      Mynav,
      PatientBase,
      RecordBar,
      Loading,
      MButton
    },
    data () {
      return {
        content_width: '',
        addBtnState: false,
        userid: '',
        projectid: '',
        mdid: '',
        templateid: '',
        addState: false,
        remarkState: false,
        imgState: false,
        postdata: {
          source_data: {},
          patientdata: {},
          imgs: '',
          remark: ''
        },
        templates: [],
        mycomponent: '',
        patientBase: {},
        records: []
      }
    },
    mounted () {
      this.projectid = this.$route.params.projectid
      this.mdid = this.$route.params.mdid
      this.content_width = document.getElementById('records').offsetLeft + document.getElementById('records').offsetWidth + 16
      window.addEventListener('resize', () => {
        this.content_width = document.getElementById('records').offsetLeft + document.getElementById('records').offsetWidth + 16
      }, false)
      this.getTemplates()
      this.getPatientBase()
      this.getPatientRecords()
    },
    methods: {
      getPatientBase () {
        API.GetPatientBase({mdid: this.mdid}).then(response => {
          this.patientBase = response[0]
        }).catch(err => {
          console.log(err)
        })
      },
      getPatientRecords () {
        this.records = []
        API.GetPatientRecords({mdid: this.mdid}).then(response => {
          response.map(n => this.records.push({
            type: n.u_templatename,
            recordid: n.u_recordid,
            templateid: n.u_templetid,
            create_time: n.u_createtime,
            update_time: n.u_updatetime,
            data: JSON.parse(n.u_patientdata),
            /* eslint-disable */
            images: eval('{'+ n.imgs +'}'),
            /* eslint-endable */
            remark: n.remark
          }))
        }).catch(err => {
          console.log(err)
        })
      },
      getTemplates () {
        API.GetpProjectTemplates({projectid: this.projectid}).then(response => {
          this.templates = response
        }).catch(err => {
          console.log(err)
        })
      },
      changeTemplate (id) {
        this.mycomponent = ''
        if (id === this.templateid) {
          this.addState = false
          this.templateid = ''
          return
        }
        Vue.component('temp', Temp['template' + id])
        this.mycomponent = 'temp'
        this.templateid = id
        this.addState = true
      },
      nextTemplate () {
        let index = 0
        for (let n in this.templates) {
          if (this.templateid === this.templates[n].u_templateid) {
            index = parseInt(n) + 1
            break
          }
        }
        if ((this.templates.length - 1) >= parseInt(index)) {
          this.changeTemplate(this.templates[index].u_templateid)
        } else {
          this.changeTemplate(this.templates[0].u_templateid)
        }
      },
      setRemarks (data) {
        this.postdata.imgs = JSON.stringify(data.imgs)
        this.postdata.remark = data.remark
      },
      setRecords (data) {
        this.postdata.source_data = data.sourceData
        this.postdata.patientdata = data.showData
      },
      postRecordData () {
        return {
          createuserid: this.$root.userid,
          mdid: this.mdid,
          templateid: this.templateid,
          patientdata: JSON.stringify(this.postdata.patientdata),
          sourcedata: JSON.stringify(this.postdata.source_data),
          imgs: this.postdata.imgs,
          remark: this.postdata.remark
        }
      },
      putData () {
        // 
        this.addBtnState = true
        const data = this.postRecordData()
        API.CreatePatientData(data).then(response => {
          this.toast({
            text: '',
            type: 'success',
            placement: 'bottom-left'
          })
          // 
          this.addBtnState = false
          this.nextTemplate()
          this.getPatientRecords()
        }).catch(err => {
          // 
          this.addBtnState = false
          this.toast({
            text: '',
            type: 'error',
            placement: 'bottom-left'
          })
          console.log(err)
        })
      },
      changeRecord (recordid, templateid) {
        this.$router.push({name: 'Record', params: {projectid: this.projectid, mdid: this.mdid, templateid: templateid, recordid: recordid}})
      }
    }
  }
</script>

<style scoped>
  .title {
    background: #fff;
    color: #333;
    font-size: 18px;
    font-weight: 300;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
  }
  .menu {
    width: 180px;
    padding: 10px;
  }

  .menu a {
    display: block;
    font-size: 16px;
    color: #777;
    padding: 15px 10px;
  }

  .content {
    width: 650px;
    margin: 0 auto;
  }


  .bar {
    padding: 10px 15px;
    justify-content: space-between;
  }

  .bar>div:first-child{
    align-items: center;
  }

  .bar>div:first-child i {
    margin-right: 10px;
  }

  i.add-remark {
    color: #3bb5ed;
    font-size: 24px;
  }

  .fixed-right {
    position: fixed;
    top: 120px;
    right: 30px;
    width: 220px;
    box-shadow: 0 1px 4px rgba(0,0,0,.04);
    border: 1px solid rgba(0,0,0,.09);
  }

  .temp-menu>div{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .temp-menu>div {
    cursor: pointer;
    border-bottom: 1px solid #eee;
    padding: 10px;
    border-left: 3px solid rgba(0,0,0,0);
  }
  .temp-menu>div.active {
    border-left-color: #3bb5ed;
  }

  .temp-menu span {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 1px solid #03d7a3;
    text-align: center;
  }

  .temp-menu span::before {
    content: '+';
    font-size: 14px;
    font-weight: 600;
    color: #03d7a3;
  }

  .temp-menu span.active {
    border: 1px solid red;
  }

  .temp-menu span.active::before {
    content: '-';
    font-size: 14px;
    font-weight: 600;
    color: red;
  }

  .temp-menu {
    color: #777;
    background: #fff;
    font-size: 14px;
    line-height: 20px;
  }

  

  .back-btn {
    text-align: center;
    display: block;
    padding: 10px 0;
    background: #fff;
    color: #03d7a3;
    font-weight: 600;
  }

  .hint {
    width: 100%;
    text-align: center;
    color: #777;
  }

  .cards {
    display: flex;
    flex-wrap: wrap;
  }

  .card {
    background: #fff;
    margin: 12px 0;
    padding: 25px;
    box-shadow: 0 1px 4px rgba(0,0,0,.04);
    border: 1px solid rgba(0,0,0,.09);
    line-height: 32px;
  }

  .card-header {
    border-bottom: 1px solid #f3f3f3;
  }

  .card-main {
    line-height: 2;
    background: #fafafa;
    margin: 10px;
    border-radius: 2px;
    border: 1px solid #e0e0e0;
    box-shadow: 0 1px 2px rgba(0,0,0,.04);
    /*max-height: 120px;*/
    /*overflow-y: hidden;*/
    cursor: pointer;
  }

  .card-remark.text{
    padding: 4px 20px;
    margin: 10px 0 12px;
    border-left: 3px solid #3cb6ec;
    line-height: 2;
    font-size: 16px;
    color: rgba(0,0,0,.64);
  }


  .input-area {
    background: #fff;
    margin-top: 10px;
    padding: 25px 25px 0 25px;
  }

  .list-enter-active, .list-leave-active {
    transition: all 1s;
  }
  .list-enter, .list-leave-active {
    opacity: 0;
    transform: translateX(30px);
  }

  .fade-enter-active, .fade-leave-active {
    transition: all .2s ease-in;
  }
  .fade-enter, .fade-leave-active {
    opacity: 0;
  }

  @media screen and (max-width: 1024px) {
    .content {
      width: 650px;
      margin: 0 0 0 100px;
    }
  }

  @media screen and (max-width: 770px) {
    .content {
      width: 500px;
      margin: 0 0 0 20px;
    }
  }
</style>


