<template>
  <div class="login" :style="{height: clientHeight + 'px'}">
    <h1>MedicaYun Patient Manage</h1>
    
    <div class="login-card">

      
      <h2 class="login-header"></h2>

      <form class="login-container">
        <p><input type="text" placeholder=""  v-model="username" ></p>
        <p><input type="password" placeholder="" v-model="password" @keyup.enter="login" ></p>
        <p><span class="submit" @click="login"></span></p>
      </form>
    </div>
  </div>
</template>

<script>
  import api from '../api.js'

  export default {
    data () {
      return {
        username: '',
        password: '',
        clientHeight: document.body.scrollHeight
      }
    },
    methods: {
      login () {
        if (!window.sessionStorage) { return }
        if (this.username === '' && this.password === '') { return }
        // session
        api.Login({username: this.username, password: this.password}).then(res => {
          window.sessionStorage.setItem('userid', res.userid)
          window.sessionStorage.setItem('authorization', res.token)
          window.sessionStorage.setItem('username', this.username)
          this.$root.userid = res.userid
          this.$router.push({path: '/'})
        })
        .catch(error => {
          console.log(error)
          console.log('error')
        })
      }
    }
  }
</script>

<style>
  .login {
    width: 100%;
    background: #456;
  }
  .login h1 {
    font-size: 20px;
    line-height: 3;
    color: #fff;
    text-align: center;
  }
  .login-card {
    margin: 0 auto;
    font-size: 16px;
  }
  .login-header,
  .login-card p {
    margin-top: 0;
    margin-bottom: 0;
  }

  /* The triangle form is achieved by a CSS hack */

  .login-header {
    background: #28d;
    padding: 20px;
    font-size: 1.4em;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
  }

  .login-container {
    background: #ebebeb;
    padding: 12px;
  }

  /* Every row inside .login-container is defined with p tags */
  .login-card p {
    padding: 12px;
  }

  .login-card input, .login-card .submit {
    box-sizing: border-box;
    display: block;
    width: 100%;
    border-width: 1px;
    border-style: solid;
    padding: 16px;
    outline: 0;
    font-family: inherit;
    font-size: 0.95em;
  }

  .login-card input[type="text"],
  .login-card input[type="password"] {
    background: #fff;
    border-color: #bbb;
    color: #555;
  }

  /* Text fields' focus effect */
  .login-card input[type="text"]:focus,
  .login-card input[type="password"]:focus {
    border-color: #888;
  }

  .login-card .submit {
    text-align: center;
    background: #28d;
    border-color: transparent;
    color: #fff;
    cursor: pointer;
  }

  .login-card .submit:hover {
    background: #17c;
  }

  /* Buttons' focus effect */
  .login-card .submit:focus {
    border-color: #05a;
  }
</style>


