<template>
  <div>
    <mynav></mynav>
    <h2 class="header-state">
      <span :class="{active: !public}" @click="public = false"></span>
      <span :class="{active: public}" @click="changeView"></span>
    </h2>

    <div class="flex-row bg-black projects" v-if="!public">
      <div class="card bg-blue" v-for="p in projects">
        <router-link :to="'/project/' + p.u_projectID + '/patients/1'">
          <div class="title">{{ p.u_projectName }}</div>
          <div class="content">{{ p.u_projectMem }}</div>
        </router-link>
      </div>
    </div>

    <div class="flex-row bg-black projects-public" v-if="public">
      <div class="card bg-blue" v-for="p in publicList">
        <div class="title">{{ p.u_projectName }}</div>
        <div class="content">{{ p.u_projectMem }}</div>
        <span v-if="p.is_join"></span>
        <m-button type="gray" v-if="!p.is_join" @click="joinTeam(p.u_projectID)"></m-button>
      </div>
    </div>
  </div>
</template>

<script>
  import API from '../api.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'
  import Datepicker from '../lib/Date.vue'

  export default {
    name: 'Home',
    components: {
      Mynav,
      MButton,
      Datepicker
    },
    data () {
      return {
        public: false,
        publicList: [],
        projects: []
      }
    },
    mounted () {
      //  List
      this.getProjects()
    },
    methods: {
      changeView () {
        this.public = true
        this.getAllProjects()
      },
      // 
      getAllProjects () {
        this.publicList = []
        API.GetAllProjects().then(res => {
          res.map(n => {
            this.publicList.push({
              u_projectMem: n.u_projectMem,
              u_projectID: n.u_projectID,
              u_projectName: n.u_projectName,
              is_join: false
            })
          })
          this.checkPublic()
        }).catch(error => {
          console.log(error)
        })
      },
      // 
      checkPublic () {
        for (let id in this.publicList) {
          // console.log(this.projects[id].u_projectID + '===' + n.u_projectID)
          for (let myid in this.projects) {
            this.projects[myid].u_projectID === this.publicList[id].u_projectID ? this.publicList[id].is_join = true : ''
          }
        }
      },
      // 
      joinTeam (id) {
        console.log(id)
        API.ApplyProject({userid: this.$root.userid, projectid: id}).then(res => {
          this.toast({
            type: 'success',
            text: '',
            placement: 'bottom-left'
          })
        }).catch(error => {
          this.toast({
            type: 'error',
            text: '',
            placement: 'bottom-left'
          })
          console.log(error)
        })
      },
      // 
      getProjects () {
        API.GetProjects({userid: this.$root.userid}).then(res => {
          res.map(n => this.projects.push(n))
        }).catch(error => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>
  .header-state {
    background: #468df1;
    height: 48px;
    color: rgba(255, 255, 255, .8);
    font-size: 16px;
    letter-spacing: 1px;
    text-align: center;
    line-height: 48px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.23);
  }

  .header-state span {
    cursor: pointer;
    display: inline-block;
    height: 45px;
    width: 180px;
  }

  .header-state span.active {
    color: #fff;
    border-bottom: 3px solid #fff;
  }

  .projects, .projects-public {
    margin-top: 24px;
    justify-content: center;
  }

  .card {
    background: #fff;
    padding: 15px 20px;
    line-height: 40px;
    width: 180px;
    margin: 10px;
  }

  .bg-blue {
    background: #3bb5ed;
  }

  .card .title {
    color: #fff;
    font-size: 16px;
    font-weight: 700;
  }

  .card .content {
    padding-top: 100px;
    font-size: 12px;
    color: rgba(0, 0, 0, 0.6);
  }

</style>


